$cover-min-heights: (25vh, 50vh, 75vh, 100vh);

.block-cover {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  &.bg-is-fixed {
    background-attachment: fixed;
  }
  &.text-white {
    h2 {
      color: inherit;
    }
  }
  @each $height in $cover-min-heights {
    &.min-h-#{$height} {
      min-height: $height;
    }
  }
  h2 {
    @each $point, $size in $cover-heading {
      @if $point == 'root' {
        // Root font size at smallest device width
        font-size: $size;
      } @else {
        // Otherwise use the breakpoint map to set the font size
        @include above($point) {
          font-size: $size;
        }
      }
    }
  }
  .block-title {
    margin-bottom: get-size(1);
  }
  .button-list {
    margin-top: get-size(3);
  }
  &.has-overlay::before {
    @include pseudo;
    width: 100%;
    height: 100%;
    //opacity: 0.8;
    top: 0;
  }
}

@each $name, $color in $background-colors {
  // Loop through $theme-colors map and create classes for .bg-*
  .overlay-#{$name}::before {
    background-color: $color;
  }
}

@for $i from 1 through 100 {
  @if $i % 10 == 0 {
    .overlay-opacity-#{$i}::before {
      opacity: $i*0.01;
    }
  }
}

.cover-text {
  @each $point, $size in $cover-text {
    @if $point == 'root' {
      // Root font size at smallest device width
      font-size: $size;
    } @else {
      // Otherwise use the breakpoint map to set the font size
      @include above($point) {
        font-size: $size;
      }
    }
  }
  line-height: 1.2;
}
